let store = sessionStorage;
let session = JSON.parse(store.NCCSessionStorage || "{}");
import ViewModel from "../shell/viewmodel/viewmodel";
let { clearBrowersStore, setCookie, getCookie } = ViewModel;

function set(key, val) {
    try {
        session[key] = val;
        store.NCCSessionStorage = JSON.stringify(session);
    } catch (e) {
        console.error("sessionStorage容量溢出");
        clear();
        // set(key, val);
    }
}

function get(key) {
    return session[key];
}

function remove(key) {
    delete session[key];
    store.NCCSessionStorage = JSON.stringify(session);
}

// function clear() {
// 	store.clear();
// 	clearBrowersStore();
// }

function clear(keysArr) {
    store.NCCSessionStorage = JSON.stringify({});
    let exceptLocalKeys = ["referHistory", 'rockin'];
    Object.keys(localStorage)
        .filter(e => {
            let res =
				// local中的所有例外(不被清除)标识  如 cowboy-*
				String(e).indexOf("cowboy") !== -1 ||
				// local中的所有例外(不被清除)标识  如 retain-*
                String(e).indexOf("retain") !== -1 ||
				exceptLocalKeys.includes(e);
            return !res;
        })
        .forEach(e => delete localStorage[e]);
    let allCookies = getCookie();
    let exceptKeys = ["userloginid", "langCode", "busiCenterCode"];
    if (keysArr && keysArr instanceof Array) {
        exceptKeys = keysArr;
    }
    for (let cookieKey in allCookies) {
        if (!exceptKeys.includes(cookieKey)) {
            setCookie(cookieKey, null, null, null, -1);
        }
    }
}

let CacheTools = {
    set,
    get,
    remove,
    clear
};
export default CacheTools;
