import React, { Component } from 'react';
import Select from 'bee-select';

require('./index.less');
require('bee-select/build/Select.css');

const Option = Select.Option;

class NCSelect extends Component {
	constructor(props) {
		super(props);
	}

	componentDidMount() {
		// 点击页面其他部分收起下拉
		// document.addEventListener('click', this.close, true);
		// document.addEventListener('scroll', this.scrollHandler,true);
	}

	componentWillUnmount() {
		// 移除document事件
		document.removeEventListener('click', this.close);

	}
	// 有这样一个方法 changeOpen --by bbqin
	// changeOpen = () => {
	//     // 有这样一个方法
	// };

	// keyDown
	keyDownHandler = (...params) => {
		// 只有一個  e對象  不過應該夠用了
		// console.log(params);
	};

	close = e => {
		const { onBlur, value, onFocus } = this.props;
		if (this.open && this.select && !this.select.contains(e.target)) {
			setTimeout(() => {
				typeof onBlur === 'function' && onBlur(value);
				this.open = false;
			}, 0);
		}
	};

	clear = () => {
		const { onChange, onSelect, onBlur, multiple } = this.props;
		typeof onChange === 'function' && onChange(undefined);
		typeof onSelect === 'function' && onSelect(undefined);
		typeof onBlur === 'function' && onBlur(multiple ? [] : undefined);
	};

	onFocus = val => {
		const { onFocus, isTable } = this.props;
		this.open = true;
		let dom = null;
		if (isTable) {
			dom = {};
		} else {
			if (
				this.select &&
				this.select.getElementsByClassName &&
				Array.isArray(this.select.getElementsByClassName('nc-select'))
			) {
				dom = this.select.getElementsByClassName('nc-select')[0];
			}
		}
		typeof onFocus === 'function' && onFocus(val, dom);
	};
	onChange = val => {
		const { onChange, value } = this.props;
		this.open = true;
		typeof onChange === 'function' && onChange(val, value);
	};
	onBlur = val => {
		this.open = false;
		const { onBlur } = this.props; // zhanghengh加 ， 否则有bug
		typeof onBlur === 'function' && onBlur(val ? [] : undefined);
	};
	render() {
		let { className, showStar, onBlur, onFocus,onClick, onChange, ...others } = this.props;
		return (
			<div
				className={`u-form-control-wrapper nc-select-wrapper ${this.props.className}`}
				ref={dom => {
					this.select = dom;
				}}
			>
				{showStar ? <span className="select-required-tag">*</span> : ''}

				<Select
					className={`nc-select ${className}`}
					onFocus={this.onFocus}
					onChange={this.onChange.bind(this)}
					onBlur={this.onBlur.bind(this)}
					// 如果设置了 autofocus  会导致跳转不出去的情况  需要注意的是 外部可能会传入autofocus  TODO  --bbqin
					ref={selectInput => {
						this.selectInput = selectInput; // 获取到组件对象 -- bbqin
					}}
					onKeyDown={this.keyDownHandler}
					
					{...others}
					notFoundContent={<div>&nbsp;</div>}
					// dropdownMatchSelectWidth={false}
				>
					{this.props.children}
					
				</Select>

				{/* {this.props.showClear &&
				!this.props.disabled &&
				((Array.isArray(this.props.value) && this.props.value.length) ||
					(this.props.value || this.props.value === 0)) && (
					<i
						className="iconfont icon-qingkong"
						onClick={this.clear}
						style={{ right: this.props.multiple ? '6px' : '24px' }}
					/>
				)} */}
			</div>
		);
	}
}
NCSelect.defaultProps = {
	showClear: true
};
NCSelect.NCOption = Option;

export default NCSelect;
